<template>
  <el-row class="container">
    <div class="selector-container" :style="{ flexDirection: layoutDir === 'horizontal' ? 'row' : 'column' }">
      <template v-for="i in 3">
        <div :key="i" class="item">
          <div class="label" :style="labelStyle">
            {{ i === 1 ? "": i === 2 ? "" : "" }}
          </div>

          <el-select v-model="value_selector[i - 1]" placeholder="" @change="changeHandler(i - 1)">
            <el-option
              v-for="item in options[i - 1]"
              :key="item['id']"
              :label="item['name']"
              :value="item['id']"
            />
          </el-select>
        </div>
      </template>
    </div>
  </el-row>
</template>

<script>
/**
 * @description  
 * @author ShuXu
 * @date 2021/02/04
 */

//      start
import {
  Row,
  Col,
  Select,
  Option
} from 'element-ui'
// end

import {
  // mapState,
  mapGetters,
  mapMutations,
  // mapActions
} from 'vuex'

export default {
  name: 'RegionSelector',
  components: {
    ElRow: Row,
    ElCol: Col,
    ElSelect: Select,
    ElOption: Option
  },
  props: {
    layoutDir: {
      // 
      type: String,
      default: () => 'horizontal' // vertical
    },
    labelWidth: {
      type: String,
      default: () => '50px'
    },
    labelAlign: {
      type: String,
      default: () => 'right'
    },
    requestList: {
      type: Array,
      required: true,
      default: () => []
    },
    responseKey: {
      type: String,
      default: () => ''
    },
    keyMapList: {
      type: Array,
      default: () => [
        ['id', 'name'],
        ['id', 'name'],
        ['id', 'name']
      ]
    },
  },
  data() {
    return {
      value_selector: [
        '', // province
        '', // city
        '' // district
      ]
    }
  },
  computed: {
    //  matState 
    // ...mapState(
    //   'regionSelector',
    //   {
    //     province: state => state.province,
    //     city: state => state.city,
    //     district: state => state.district
    //   }
    // ),

    //  mapGetters 
    ...mapGetters(
      'regionSelector',
      [
        'province',
        'city',
        'district'
      ]
    ),

    options: function() {
      return [
        this['province'],
        this['city'],
        this['district']
      ]
    },

    labelStyle: function() {
      return {
        width: this.labelWidth,
        textAlign: this.labelAlign,
        padding: '0 12px'
      }
    }
  },
  mounted() {},
  methods: {
    ...mapMutations(
      'regionSelector',
      {
        'setProvince': 'SET_PROVINCE',
        'setCity': 'SET_CITY',
        'setDistrict': 'SET_DISTRICT'
      }
    ),

    /**
     *  region data
     * @param index
     */
    changeHandler: function(index) {
      for (let i = 3; i--;) { // 2 1 0
        if (i > index) this.value_selector[i] = '' //  
      }
      if (index !== 2) this.initNext(index + 1) //   list
      // this.$emit('change', index)
    },

    init: function () {
      this.initNext(0)
    },

    /**
     * 
     * @returns {string[]}
     */
    getValue: function() {
      return this.value_selector
    },

    /**
     *   list    
     * @param regionData
     * @returns {Promise<void>}
     */
    setValue: async function(regionData) {
      for ( let i in [0, 1, 2] ) { // 0 1 2
        i = parseInt(i)
        //    
        if (i !== 0) {
          await this.doGetRegionData(i)
        }
        this.value_selector[i] = regionData[i]
        this.$forceUpdate()
      }
    },

    /**
     *   list
     * @param level 0= 1= 2= 'init'=
     * @returns {Promise<void>}
     */
    initNext: async function(level) {
      await this.doGetRegionData(level)
    },

    /**
     *   
     * @param level
     * @returns {Promise<unknown>}
     */
    doGetRegionData: function (level) {
      const that = this
      return new Promise(resolve => {
        that.requestList[level]().then(res => {
          if (res) {
            const response = that.responseKey ? res[that.responseKey] : res
            const ls = []
            response.map(item => {
              ls.push({
                id: item[that.keyMapList[level][0]],
                name: item[that.keyMapList[level][1]]
              })
            })

            that.doSetRegionData(ls, level)
          }

          resolve(true)
        })
      })
    },

    /**
     *    store
     * @param value
     * @param level
     */
    doSetRegionData: function (value, level) {
      const mutationList = [
        'setProvince',
        'setCity',
        'setDistrict'
      ]
      this[mutationList[level]](value)
    }
  }
}
</script>

<style lang="scss" scoped>
.container {
  .selector-container {
    width: 100%;
    display: flex;

    .item {
      margin-bottom: 22px;
      flex: 1;

      .label {
        display: inline-block;
        font-size: 14px;
        color: #606266;
        line-height: 40px;
      }
    }
  }
}
</style>
